import Layout from "./../components/Layout";
import Link from "next/link";

const PostLink = ({ title }) => (
  <li>
    <Link href={`/post?title=${title}`}>
      <a>{title}</a>
    </Link>
  </li>
);

export default () => (
  <Layout title="Myblog">
    <PostLink slug="react-post" title="React-Post" />
    <PostLink slug="angular-post" title="Angular Post" />
    <PostLink title="vue" />
  </Layout>
);
